<template>
	<view class="help">
		<view class="box" v-for="(item, index) in helpCenter">
			<view class="tit"><div class="bg">Q{{index+1}}</div> <text>问题{{index+1}}:</text>{{ item.title }}</view>
			<text class="text">{{ item.content }}</text>
		</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';

export default {
	data() {
		return {
			helpCenter: {}
		};
	},
	onShow() {
		this.getData();
	},
	methods: {
		getData() {
			this.request(API.helpCenter, 'GET', {}, true).then(res => {
				if (res.code === 10000) {
					this.helpCenter = res.data;
				}
			});
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/user/help.scss';
</style>
